*{
    margin: 0;padding: 0;
  }
  body{
      background-color: black;
  } 
  .container{
      background-color: #c8b8e3;
      color: rgb(5, 5, 5);
      border: 3px solid black;
      min-height: 50vh;
      width: 550px;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: auto;
      position: relative;
      top: 16vh;
      border: 8px solid rgb(166 116 199);
      
  }
  .container input{
      color: rgb(12, 12, 12);
      border: 2px solid black;
      margin-bottom: 6px;
      margin-top: 6px;
      height: 20px;
      cursor: pointer;
  }
  .container button{
      color: rgb(10, 10, 10);
      border: 2px solid black;
      margin: 9px 0;
      height: 24px;
      cursor: pointer;
      
      
  }
  button:hover{
      background-color: rgb(125, 224, 224);
      color: black;
      width: 130px;
      height: 30px;
      font-size: medium;
      font-weight: bold;
      
  }
  .inputAB {
      display: flex;
      align-items: center;
      width:50% ;
      
  }
  .inpA{
      background-color: #ccecf7;
      margin: 0 10px;
      width: 100px;
      border: 3px outset rgb(252, 252, 252);
    }
    .inpA input{
       width: 92px;
    }
    .inpB{
        background-color: #97dfcd;
        width: 100px;
        margin: 0 20px;
        border: 3px outset rgb(250, 249, 249);
    }
    .inpB input{
        width: 92px;
    }
    .result{
      background-color: #f5ecec;
      color: black;
      border: 3px solid black;
      min-width: 54px;
  }
  